import React from 'react';
import { graphql, Link } from 'gatsby';

import Layout from '../components/layout';
import SEO from '../components/seo';
import BlogCard from '../components/blogCard';

import { getLocalizedPath } from '../utils';

import suscribeIcon from '../images/icons/subscribe.svg';

interface Props {
  pageContext: {
    locale: string;
    canonical: boolean;
    alternatePages: {
      slug: string;
      locale: string;
    }[];
  };
  data: {
    post: {
      html: string;
      frontmatter: {
        title: string;
        description: string;
        thumbnail: string;
      };
    };
    posts: {
      nodes: {
        frontmatter: {
          title: string;
          description: string;
          thumbnail: string;
        };
        fields: {
          localizedSlug: string;
        };
      }[];
    };
  };
}

const BlogPost = ({ pageContext, data }: Props) => {
  const { locale, canonical, alternatePages } = pageContext;
  const post = data.post;
  const lastPosts = data.posts.nodes;
  return (
    <>
      <SEO
        title={post.frontmatter.title}
        image={post.frontmatter.thumbnail}
        locale={locale}
        canonical={canonical}
        alternatePages={alternatePages}
      />
      <Layout locale={locale}>
        <div>
          <div className="w-full">
            <div className="bg-blue absolute h-screen w-full bg-opacity-40"></div>
            <div className="absolute w-full text-center top-48">
              <h1 className="text-white text-7xl md:text-9xl md:px-8">{post.frontmatter.title}</h1>
              <p className="container text-white">{post.frontmatter.description}</p>
            </div>

            <img
              src={post.frontmatter.thumbnail}
              className="object-cover object-bottom h-screen w-full"
            />
          </div>
          <div className="container py-24 ">
            <div className="content shadow rounded text-black py-4 break-words px-8 md:px-48 md:py-20 bg-white" dangerouslySetInnerHTML={{ __html: post.html }} />
            {lastPosts.length > 0 && (
              <div className="container bg-blue rounded p-16 mt-20">
                <h3 className="text-4xl font-semibold text-white">Last posts</h3>
                <Link to={getLocalizedPath(locale, '/blog')} className="flex mt-6">
                  <img src={suscribeIcon} className="icon-white transform rotate-180 w-6" />
                  <p className="px-2 text-white">See all posts</p>
                </Link>
                <div className="py-8 lg:flex lg:flex-wrap lg:justify-start">
                  {lastPosts.map(article => (
                    <BlogCard
                      link={article.fields.localizedSlug}
                      thumbnail={article.frontmatter.thumbnail}
                      title={article.frontmatter.title}
                      description={article.frontmatter.description}
                    />
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      </Layout>
    </>
  );
};

export default BlogPost;

export const query = graphql`
  query ($id: String!, $locale: String!) {
    post: markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        title
        description
        thumbnail
      }
    }
    posts: allMarkdownRemark(
      limit: 3
      filter: {
        id: { ne: $id }
        fileAbsolutePath: { regex: "/blog/" }
        fields: { locale: { eq: $locale } }
      }
      sort: { fields: frontmatter___date, order: DESC }
    ) {
      nodes {
        frontmatter {
          title
          description
          thumbnail
        }
        fields {
          localizedSlug
        }
      }
    }
  }
`;
